<template>
	<div class="sy_bao animated fadeIn">
		<div id="myChart" :style="{width: '800px', height: '800px'}" style="float:left"></div>
		<div id="myChart2" :style="{width: '800px', height: '800px'}" style="float:right"></div>
	</div>
</template>
<script>
	// 引入基本模板
	let echarts = require("echarts/lib/echarts");
	// 引入柱状图组件
	require("echarts/lib/chart/bar");
	// 引入提示框和title组件
	require("echarts/lib/component/tooltip");
	require("echarts/lib/component/title");
	export default {
		name: "hello",
		data() {
			return {
				msg: "Welcome to Your Vue.js App"
			};
		},
		mounted() {
			this.drawLine();
		},
		methods: {
			drawLine() {
				// 基于准备好的dom，初始化echarts实例
				let myChart = this.$echarts.init(document.getElementById('myChart'))
				let myChart2 = this.$echarts.init(document.getElementById('myChart2'))
				// 绘制图表
				myChart.setOption({
					title: {
						// text: '世界人口总量',
						// subtext: '数据来自网络'
					},
					tooltip: {
						trigger: 'axis',
						axisPointer: {
							type: 'shadow'
						}
					},
					legend: {
						data: ['2011年', '2012年']
					},
					grid: {
						left: '3%',
						right: '4%',
						bottom: '3%',
						containLabel: true
					},
					xAxis: {
						type: 'value',
						boundaryGap: [0, 0.01]
					},
					yAxis: {
						type: 'category',
						data: ['巴西', '印尼', '美国', '印度', '中国', '世界人口(万)']
					},
					series: [{
							name: '2011年',
							type: 'bar',
							data: [18203, 23489, 29034, 104970, 131744, 630230]
						},
						{
							name: '2012年',
							type: 'bar',
							data: [19325, 23438, 31000, 121594, 134141, 681807]
						}
					]
				});

				myChart2.setOption({
					title: {
						
						
						x: 'center'
					},
					tooltip: {
						trigger: 'item',
						formatter: "{a} <br/>{b} : {c} ({d}%)"
					},
					legend: {
						// orient: 'vertical',
						left: 'center',
						data: ['直接访问', '邮件营销', '联盟广告', '视频广告', '搜索引擎']
					},
					series: [{
						name: '访问来源',
						type: 'pie',
						radius: '55%',
						center: ['50%', '50%'],
						data: [{
								value: 335,
								name: '直接访问'
							},
							{
								value: 310,
								name: '邮件营销'
							},
							{
								value: 234,
								name: '联盟广告'
							},
							{
								value: 135,
								name: '视频广告'
							},
							{
								value: 1548,
								name: '搜索引擎'
							}
						],
						itemStyle: {
							emphasis: {
								shadowBlur: 10,
								shadowOffsetX: 0,
								shadowColor: 'rgba(0, 0, 0, 0.5)'
							}
						}
					}]
				});

			}
		}
	};
</script>

<style>
	.sy_bao {
		background: #fff;
		height: 500px;
		padding: 30px;
	}
</style>


